/* global kakao*/
import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { readPhoto, initWish, wishRoom, unwishRoom, selectRoom } from '../modules/room.jsx'
import { changeStatus } from '../modules/setting.jsx'
import TextSearch from '../components/TextSearch.jsx'
import WishList from '../components/WishList.jsx'
import Room from '../components/Room.jsx'
import AdvancedSearch from '../components/AdvancedSearch.jsx'
import SearchResult from '../components/SearchResult.jsx'
import { getAdRoom, getOneRoom, getPhoneNumber, checkOneWish, unCheckOneWish, getRoomsWithOption } from '../services/api.jsx'
import ShareRoom from '../components/ShareRoom.jsx'
import { cancel } from 'redux-saga/effects'

const FIRST_CONSONANT = ['ㄱ', 'ㄲ', 'ㄴ', 'ㄷ', 'ㄸ', 'ㄹ', 'ㅁ', 'ㅂ', 'ㅃ', 'ㅅ', 'ㅆ', 'ㅇ', 'ㅈ', 'ㅉ', 'ㅊ', 'ㅋ', 'ㅌ', 'ㅍ', 'ㅎ'];

const ContentsContainer = ({ setLng, setLat, setName }) => {
  const { roomInfo, photo, wish, room, status, prevStatus, ad } = useSelector(({ room, setting }) => ({
    room: room.room,
    roomInfo: room.roomInfo,
    photo: room.photo,
    wish: room.wish,
    ad: room.ad,
    status: setting.status,
    prevStatus: setting.prevStatus,
  }))
  const [condition, setCondition] = useState({
    location: [],
    isMany: -1,
    monthFee: -1,
    charterFee: -1,
    halfFee: -1,
    isLH: -1,
    friend: -1,
    family: -1,
    isElevator: -1,
    isParking: -1,
    isCctv: -1,
    isPet: -1,
  })

  const [touched, setTouched] = useState(false)
  const [showAll, setShowAll] = useState(false)
  const [modal, setModal] = useState(false)
  const [wishList, setWishList] = useState([])
  const [search, setSearch] = useState([])
  const [text, setText] = useState("");
  const [result, setResult] = useState([])

  const dispatch = useDispatch()
  const checkWish = (id) => {
    checkOneWish(id)
    dispatch(wishRoom(id))
  }
  const unWish = (id) => {
    dispatch(unwishRoom(id))
    unCheckOneWish(id)
  }
  const handleStatus = (id) => { dispatch(changeStatus(id)) }
  const handleSearch = () => {
    setSearch(filtered());
    getRoomsWithOption(condition)
    handleStatus(4);
  }
  const getConsonant = (first) => { return FIRST_CONSONANT[parseInt((first.charCodeAt() - 44032) / 588)] }

  const handleText = (e) => {
    if (e.target.value[0] === " ")
      e.target.value = e.target.value.replace(" ", "")
    setText(e.target.value)
  }

  const searchAll = () => {
    setShowAll(!showAll);
  }
  const handleModal = () => { setModal(!modal) }
  const cancelAll = () => { setShowAll(false) }

  const handleReset = () => {
    setCondition({
      location: [], roomType: false, isMany: 0, isLH: false, halfPrice: false, monthPrice: false, yearPrice: false, isElevator: false, isParking: false, isCctv: false, isPet: false,
    })
    //change plz
  }

  const handlePhone = (id) => {
    getPhoneNumber(id)
  }


  const onChangeCondition = (type, value) => {
    if (type === 'location') {
      value = parseInt(value)
      if (condition[type].indexOf(value) !== -1) setCondition({ ...condition, [type]: condition[type].filter(c => c !== value) })
      else setCondition({ ...condition, [type]: [...condition[type], value] })
    } else {
      if (condition[type] === -1) setCondition({ ...condition, [type]: true })
      else setCondition({ ...condition, [type]: !condition[type] })
    }
  }

  const filtered = () => {
    let result = []
    room.map(r => {
      let data = r
      if (condition.location.length !== 0 && condition.location.indexOf(r.locationId) === -1) data = ''
      if (condition.friend === true && condition.family !== true && r.isMany !== 1) data = ''
      if (condition.family === true && r.isMany !== 2) data = ''
      if (condition.monthFee === true && !!r.monthPrice === false) data = ''
      if (condition.charterFee === true && !!r.charterPrice === false) data = ''
      if (condition.halfFee === true && !!r.halfPrice === false) data = ''
      if (condition.isElevator === true && r.isElevator === false) data = ''
      if (condition.isParking === true && r.isParking === false) data = ''
      if (condition.isCctv === true && r.isCctv === false) data = ''
      if (condition.isPet === true && r.isPet === false) data = ''
      if (condition.isLH === true && r.isLH === false) data = ''
      if (data !== '') result.push(data)
    })
    return sortingWithConsonant(result)
  }

  const sortingWithConsonant = (result) => {
    let list = []
    result = result.sort((x, y) => { return (x.name[0] < y.name[0]) ? -1 : x.name < y.name ? 1 : 0 })
    for (let i = 0; i < result.length; i++) {
      let f = getConsonant(result[i].name[0])
      if (list.length === 0) list.push({ id: f, entries: [result[i]] })
      else {
        for (let j = 0; j < list.length; j++) {
          if (list[j].id === f) { list[j].entries.push(result[i]); break; }
          else { if ((list.length - 1) === j) { list.push({ id: f, entries: [result[i]] }); break; } }
        }
      }
    }
    return list
  }

  const onSelectRoom = (id, type = '') => {
    let r = room.filter(r => r.id === id)[0];
    (type === 'ad') ? getAdRoom(r.id) : getOneRoom(r.id)
    dispatch(selectRoom(r))
    handleStatus(3)
  }

  const filterWishList = () => {
    let a = []
    wish.map(w => {
      room.map(r => {
        if (w === r.id)
          a.push({ id: r.id, name: r.name, location: r.locationId, price: r.yearPrice, latitude: r.latitude, longitude: r.longitude })
      })
    })
    return a
  }

  const movePrevious = () => {
    if (prevStatus == 3) {
      handleStatus(0)
    }
    else (
      handleStatus(prevStatus)
    )
  }

  useEffect(() => {
    let localWish = JSON.parse(localStorage.getItem("wish"))
    if (localWish === null)
      localWish = []
    dispatch(initWish(localWish));
  }, [])

  useEffect(() => {
    setText("")
    if (status === 2)
      setWishList(filterWishList());
  }, [status])

  useEffect(() => {
    if (wish) {
      setWishList(filterWishList());
      localStorage.setItem("wish", JSON.stringify(wish))
    } else {
      localStorage.setItem("wish", "[]")
    }
    if (roomInfo !== null) {
      dispatch(readPhoto(roomInfo.id))
      if (wish !== null) wish.indexOf(roomInfo.id) !== -1 ? setTouched(true) : setTouched(false)
      else setTouched(false)
    }
  }, [roomInfo, wish])

  useEffect(() => {
    let result = []
    room.map(r => { if (r.name.indexOf(text) !== -1 && text !== "") result.push(r) })
    setResult(sortingWithConsonant(result))

  }, [text])

  useEffect(() => {
    showAll === true ? setResult(sortingWithConsonant(room)) : setResult(sortingWithConsonant([]))
  }, [showAll])


  return (
    <>
      {status === 0 ? <TextSearch ad={ad} result={result} text={text} handleText={handleText} onSelectRoom={onSelectRoom} searchAll={searchAll} status={status} cancelAll={cancelAll} setLat={setLat} setLng={setLng} setName={setName} /> : ""}
      {status === 1 ? <AdvancedSearch onChangeCondition={onChangeCondition} handleSearch={handleSearch} condition={condition} handleReset={handleReset} handleStatus={handleStatus} /> : ""}
      {status === 2 ? <WishList wishList={wishList} unWish={unWish} onSelectRoom={onSelectRoom} setLat={setLat} setLng={setLng} setName={setName} /> : ""}
      {status === 3 ? <Room handlePhone={handlePhone} handleModal={handleModal} modal={modal} wish={wish} roomInfo={roomInfo} photo={photo} checkWish={checkWish} unWish={unWish} touched={touched} movePrevious={movePrevious} /> : ""}
      {status === 4 ? <SearchResult ad={ad} search={search} onSelectRoom={onSelectRoom} handleStatus={handleStatus} setLat={setLat} setLng={setLng} setName={setName} /> : ""}
      {status === 5 ? <ShareRoom /> : ""}
    </>
  )
}

export default ContentsContainer