import { createAction, handleActions } from 'redux-actions'
import { takeLatest } from 'redux-saga/effects'
import createRequestSaga, { createRequestActionTypes } from '../libs/createRequestSaga.jsx'
import { getAd, getRooms, getAreas, getPhotos } from '../services/api.jsx'

const SELECT_ROOM = 'room/SELECT_ROOM'
const INIT_WISH = 'room/INIT_WISH'
const WISH_ROOM = 'room/WISH_ROOM'
const UNWISH_ROOM = 'room/UNWISH_ROOM'
const [READ_PHOTO, READ_PHOTO_SUCCESS, READ_PHOTO_FAILURE] = createRequestActionTypes('room/READ_PHOTO')
const [READ_ROOM, READ_ROOM_SUCCESS, READ_ROOM_FAILURE] = createRequestActionTypes('room/READ_ROOM')
const [READ_AD, READ_AD_SUCCESS, READ_AD_FAILURE] = createRequestActionTypes('room/READ_AD')
const [READ_AREA, READ_AREA_SUCCESS, READ_AREA_FAILURE] = createRequestActionTypes('room/READ_AREA')

export const selectRoom = createAction(SELECT_ROOM, room => room)
export const initWish = createAction(INIT_WISH, room => room)
export const wishRoom = createAction(WISH_ROOM, id => id)
export const unwishRoom = createAction(UNWISH_ROOM, id => id)
export const readPhoto = createAction(READ_PHOTO, id => id)
export const readRoom = createAction(READ_ROOM)
export const readAd = createAction(READ_AD)
export const readArea = createAction(READ_AREA)

const readRoomSaga = createRequestSaga(READ_ROOM, getRooms)
const readAdSaga = createRequestSaga(READ_AD, getAd)
const readAreaSaga = createRequestSaga(READ_AREA, getAreas)
const readPhotoSaga = createRequestSaga(READ_PHOTO, getPhotos)

export function* roomSaga() {
  yield takeLatest(READ_AD, readAdSaga)
  yield takeLatest(READ_ROOM, readRoomSaga)
  yield takeLatest(READ_AREA, readAreaSaga)
  yield takeLatest(READ_PHOTO, readPhotoSaga)
}

const initialState = {
  ad: [],
  room: [],
  area: [],
  photo: [],
  wish: [],
  roomInfo: null,
  roomError: null,
  adError: null,
  areaError: null,
  photoError: null,
}

const room = handleActions({
  [READ_AD_SUCCESS]: (state, { payload: ad }) => ({
    ...state,
    ad: ad.result
  }),
  [READ_AD_FAILURE]: (state, { payload: error }) => ({
    ...state,
    adError: error
  }),
  [READ_ROOM_SUCCESS]: (state, { payload: room }) => ({
    ...state,
    room: room.result
  }),
  [READ_ROOM_FAILURE]: (state, { payload: error }) => ({
    ...state,
    roomError: error
  }),
  [SELECT_ROOM]: (state, { payload: room }) => ({
    ...state,
    roomInfo: room
  }),
  [INIT_WISH]: (state, { payload: room }) => ({
    ...state,
    wish: room
  }),
  [WISH_ROOM]: (state, { payload: room }) => ({
    ...state,
    wish: state.wish.concat(room)
  }),
  [UNWISH_ROOM]: (state, { payload: room }) => ({
    ...state,
    wish: state.wish.filter(w => w !== room)
  }),
  [READ_AREA_SUCCESS]: (state, { payload: area }) => ({
    ...state,
    area: area.result
  }),
  [READ_AREA_FAILURE]: (state, { payload: error }) => ({
    ...state,
    areaError: error
  }),
  [READ_PHOTO_SUCCESS]: (state, { payload: photo }) => ({
    ...state,
    photo: photo.result
  }),
  [READ_PHOTO_FAILURE]: (state, { payload: error }) => ({
    ...state,
    photoError: error
  }),
}, initialState)

export default room