import React from "react";
import styled from "styled-components";
import Logo from "../static/images/Logo.png";
import { Link } from "react-router-dom";

const Header = ({ handleStatus, handleAssign }) => {
  const assign = () => {
    handleAssign()
    handleStatus(5)
  }


  return (
    <HeaderContainer id="qwer">
      <StyledLink>
        <LogoSection href="javascript:window.location.reload(true)">
          <img src={Logo} alt="Logo"></img>
        </LogoSection>
      </StyledLink>
      <MenuContainer>
        <MenuList>
          <Menu onClick={() => handleStatus(1)}>검색조건</Menu>
        </MenuList>
        <MenuList>
          <Menu onClick={() => handleStatus(2)}>찜 목록</Menu>
        </MenuList>
        <MenuList>
          <Menu onClick={assign}>방 양도</Menu>
        </MenuList>
        <MenuList>
          <MenuLink onClick={() => handleStatus(-1)} to="/Intro">팀 소개</MenuLink>
        </MenuList>
      </MenuContainer>
    </HeaderContainer >
  );
};


const StyledLink = styled.a`
  width: 100%;
  height: 100%;
`

const HeaderContainer = styled.div`
  display: flex;
  background-color: #ffffff;
  box-shadow: 5px 5px 5px #00000029;
  z-index: 100;
  position: sticky;
`;

const LogoSection = styled.a`
  flex-basis: 50%;
  > img {
    padding: 0 1%;
    width: 8%;
    cursor: pointer;
    margin-top: 4px;
    @media screen and (min-width: 834px) and (max-width: 1439px) {
      margin-top: 6px;
    }
    @media screen and (min-width: 834px) and (max-width: 1439px)  { 
      width: 15%;
  }
    @media screen and (min-width: 1440px){
      width: 13%;
    }
  @media (min-width:2500px){
    width:6%;
  }
  }
`

const MenuContainer = styled.ul`
  flex-basis: 50%;
  display: flex;
  align-items: center;
  justify-content:flex-end;
    margin-right: 3rem;
  @media only screen and (min-width: 834px)   { 
  flex-basis:100%;
  }
  @media screen and (min-width: 1600px){
    margin-left: 15rem;
    justify-content:flex-end;
    margin-right: 3rem;
  }

  
`;

const MenuList = styled.li`
  display: flex;
  font-size: 23px;
  text-align: center;
  align-items: center;
  text-decoration: none;
  list-style: none;
  margin-left: 10%;
  font-family: s-core-dream-regular;
  color: black;
  cursor: pointer;
  @media screen and (min-width: 480px) and (max-width: 1439px) {
    font-size: 18px;
  }


`;

const Menu = styled.div`

`;

const MenuLink = styled(Link)`
  text-decoration: none;  
  color: black;
`;

export default Header;
