import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { readAd, readRoom, readArea, selectRoom } from '../modules/room.jsx'
import { changeStatus } from '../modules/setting.jsx'
import MapInfo from '../components/MapInfo.jsx'
import { getOneRoom } from '../services/api'

const MapInfoContainer = ({ lat, lng, zoomLevel, setZoomLevel, name, setName }) => {
  const { room, area, photo, } = useSelector(({ room }) => ({
    room: room.room,
    area: room.area,
    photo: room.photo
  }))

  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(readRoom())
    dispatch(readAd())
    dispatch(readArea())
  }, [])

  const onSelectRoom = (r) => {
    getOneRoom(r.id)
    dispatch(selectRoom(r))
    dispatch(changeStatus(3))
  }

  return (
    <MapInfo room={room} area={area} photo={photo} onSelectRoom={onSelectRoom} lat={lat} lng={lng} zoomLevel={zoomLevel} setZoomLevel={setZoomLevel} name={name} setName={setName} />
  )
}

export default MapInfoContainer