import React from "react";
import styled from "styled-components";
import ConsonantList from '../common/ConsonantList.jsx'
import SearchIcon from '../static/icons/search.svg'
import HambergerIcon from '../static/icons/hamberger.svg'

const TextSearch = ({ ad, result, text, handleText, onSelectRoom, searchAll, cancelAll, setLat, setLng, status, setName }) => {

  const onBlur = (e) => {
    e.target.value = ''
  }

  return (
    <>
      <Container>
        <Header>
          <Icon src={HambergerIcon} onClick={searchAll} />
          <Input type="text" size="25" onChange={handleText} placeholder="원하시는 빌라명을 입력해주세요" onFocus={cancelAll} onBlur={onBlur} />
          <Icon src={SearchIcon} />
        </Header>
        <ConsonantList ad={ad} result={result} text={text} onSelectRoom={onSelectRoom} setLat={setLat} setLng={setLng} status={status} setName={setName} />
        <Margin />
      </Container>
    </>
  );
};
const Margin = styled.div`
margin-bottom:100px;
`

const Container = styled.div`
  width: 36%;
  margin: 0 auto;
  overflow-y: scroll;
  overflow-x: hidden;
  display: block;
`;

const Header = styled.div`
  width: 84%;
  margin: 20px auto;
  box-shadow: 5px 5px 5px #00000029;
  border-radius: 5px;
  padding: 12px 20px;
  background-color: #f5f7ff;
  display: flex;
`;

const Input = styled.input`
  background: none;
  border: none;
  width: 100%;
  font-size: 16px;
  font-family: s-core-dream-Light;
  flex: 1 1 92%;
  margin: 0 20px;
  &:focus {
    outline: none;
  }
`;

const Icon = styled.img`
  flex: 1 1 4%;
  cursor: pointer;
`

export default TextSearch;