import React, {useState} from "react";
import MapInfoContainer from "../containers/MapInfoContainer";
import ContentsContainer from "../containers/ContentsContainer";


const Main = () => {
  const [lat, setLat] = useState("36.77454690595654");
  const [lng, setLng] = useState("126.93253082534099");
  const [zoomLevel, setZoomLevel] = useState(3);
  const [name, setName] = useState();


  return (
    <>
      <MapInfoContainer lat={lat} lng={lng} zoomLevel={zoomLevel} setZoomLevel={setZoomLevel} name={name} setName = {setName}/>
      <ContentsContainer setLat={setLat} setLng={setLng} setName = {setName} />
    </>
  );
};

export default Main;
