import React from "react";
import styled from "styled-components";
import HeartBox from "../static/icons/HeartBox.svg";
import CheckList from "../static/icons/CheckList.svg";
import FillHeart from "../static/icons/FillHeart.svg";
import NotFound from '../common/NotFound.jsx'

const colors = ["#0D3692", "#33A23D", "#FE5D10", "#00A2D1"]

const WishList = ({ wishList, unWish, onSelectRoom, setLat, setLng, setName }) => {
  return (
    <WishListContainer>
      <HeaderContainer>
        <ListIcon>
          <img src={HeartBox} />
        </ListIcon>
        <HeaderTitle>찜 목록</HeaderTitle>
      </HeaderContainer>
      <TitleList>
        <NameTitle>이름</NameTitle>
        <LocationTitle>구역</LocationTitle>
        <PriceTitle>년세<span>(만원)</span></PriceTitle>
        <WishTitle><img src={CheckList} alt="CheckedList" /></WishTitle>
      </TitleList>
      <ListContainer>
        {wishList.length !== 0 ? wishList.map(w => {
          return (<>
            <Entity key={w.id}>
              <Name onClick={() => {
                onSelectRoom(w.id);
                setLat(w.latitude);
                setLng(w.longitude);
                setName(w.name);
              }}>{w.name}</Name>
              <Location>
                <Badge id={w.location}>{w.location}구역</Badge>
              </Location>
              <Price>{w.price}</Price>
              <Wish><img src={FillHeart} alt="touchedLike" onClick={() => unWish(w.id)} /></Wish>
            </Entity></>
          )
        }) : <NotFound />}
      </ListContainer>
    </WishListContainer>
  );
};
const WishListContainer = styled.div`
  width: 36%;
  display: block;
  overflow-y: auto;
  overflow-x: hidden;
  padding-bottom: 100px;
`;

const HeaderContainer = styled.div`
  background: #dce2ff;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 18px 0;
`;

const ListIcon = styled.div`
  flex: 1;
  text-align: right;
  padding-right: 14px;
  > img {
    width: 28px;
  }
  @media screen and (min-width: 480px) and (max-width: 1440px) {
    > img {
      width: 24px;
    }
  }
`;

const HeaderTitle = styled.div`
  flex-basis: 50%;
  font-size: 28px;
  font-family: 's-core-dream-medium';

  @media screen and (min-width: 480px) and (max-width: 1440px) {
    font-size: 24px;
  }
`;

const TitleList = styled.div`
  display: flex;
  margin: 0 auto;
  width: 100%;
  background: #f5f7ff;
  font-size: 24px;
  align-items: center;
  text-align: center;
  padding: 10px;
  font-family: 's-core-dream-medium';
  @media screen and (min-width: 480px) and (max-width: 1440px) {
    font-size: 18px;
  }
`;
const NameTitle = styled.div`
  flex: 2;
  
`;
const LocationTitle = styled.div`
  flex: 1.5;

`;
const PriceTitle = styled.div`
  flex: 2;
  > span {
    font-size: 14px;
  }
`;
const WishTitle = styled.div`
  border-right:1px solid #707070;
  flex: 1.5;
  > img {
    width: 20px;
  }
  @media screen and (min-width: 480px) and (max-width: 1440px) {
    > img {
      width: 14px;
    }
  }

  
`;

const ListContainer = styled.div`
  display: flex;
  flex-direction: column;
`;
const Entity = styled.div`
  flex: 1;
  display: flex;
  margin: 0 auto;
  padding: 10px;
  font-size: 20px;
  width: 100%;
  align-items: center;
  text-align: center;
  font-family: 's-core-dream-regular';
  & {
    border-bottom: 1px solid #E9E9E9;
  }
  @media screen and (min-width: 480px) and (max-width: 1440px) {
    font-size: 18px;
  }
`;

const Name = styled.div`
  flex: 2;
  text-align: center;
  cursor: pointer;
`;

const Location = styled.div`
  flex: 1.5;
`;

const Badge = styled.div`
  width: 40%;
  border: 2px solid ${props => colors[props.id - 1] || 'black'};
  border-radius: 8px;
  font-size: 16px;
  margin: 0 auto;
  line-height: 1.4;
  font-family:s-core-dream-medium;
  @media screen and (min-width: 480px) and (max-width: 1440px) {
    font-size: 14px;
    padding: 1px 2px;
  }
`

const Price = styled.div`
  flex: 2;
`;

const Wish = styled.div`
  flex: 1.5;
  cursor: pointer;
  > img {
    width: 26px;
  }
`;


export default React.memo(WishList);
