import React, { useState } from 'react'
import styled from 'styled-components'
import NotFound from "./NotFound.jsx";
import promotion from "../static/icons/promotion.svg"
const colors = ["#0D3692", "#33A23D", "#FE5D10", "#00A2D1"]

const ConsonantList = ({ ad, result, text, onSelectRoom, setLat, setLng, status, setName }) => {
  const [isClick, setIsClick] = useState(false)

  const onClick = () => {
    setIsClick(true);

    setTimeout(function () {
      setIsClick(false);
    }, 2000);
  }

  let filterAd = [];

  if (status !== 0) {
    for (let r = 0; r < ad.length; r++) {
      for (let j = 0; j < result.length; j++) {
        for (let k = 0; k < result[j].entries.length; k++) {
          if (ad[r].name === result[j].entries[k].name) {
            filterAd.push(ad[r]);
          }
        }
      }
    }
  }
  else {
    for (let r = 0; r < ad.length; r++) {
      filterAd.push(ad[r])
    }
  } // 프로모션 상품중 검색 조건에 해당되는 것만 출력하도록 함




  let randomAd = filterAd;

  for (let i = 0; i < randomAd.length; i++) {

    let j = Math.floor(Math.random() * (i + 1));
    [randomAd[i], randomAd[j]] = [randomAd[j], randomAd[i]];
  } // 광고 랜덤으로 나오게 하는 임시코드 , 프로모션 버튼 클릭시 재랜더링 되며 광고 순서가 바뀌어 버림 수정 필요

  return (
    <>
      {result.length === 0 ? (text === "" ? '' : <NotFound msg="검색 결과를 찾을 수 없습니다." />) : ''}
      <Topic>
        <Promotion><PromotionIcon src={promotion} onClick={onClick} />프로모션 {isClick ? <PromotionText>해당 영역은 광고상품 영역으로 무작위 순서로 배열됩니다.</PromotionText> : null}</Promotion>
        {randomAd.map(a => {

          return (<RoomList key={a.roomId}>
            <RoomName onClick={() => {
              onSelectRoom(a.roomId, 'ad');
              setLat(a.latitude);
              setLng(a.longitude);
              setName(a.name);
            }} >{a.name}</RoomName>
            <Zone>{a.locationId}구역</Zone>
          </RoomList>)
        })}
      </Topic>
      {
        result.map((s, i) => {
          return (<Topic key={i}>
            <Topbar key={s.id}>{s.id}</Topbar>
            {s.entries.map((e) => {
              return (<div key={e.name}>
                {e.name ? (
                  <RoomList>
                    <RoomName onClick={() => {
                      onSelectRoom(e.id);
                      setLat(e.latitude);
                      setLng(e.longitude);
                      setName(e.name)
                    }}>{e.name}</RoomName>
                    <Zone id={e.locationId}>{e.locationId}구역</Zone>
                  </RoomList>
                ) : ''}</div>);
            })}
          </Topic>)
        })
      }
    </>);
};

const PromotionIcon = styled.img`
margin-right: 5px;
cursor: pointer;
border-radius: 50%;
:hover{
  width: 19px;
  height: 19px;
  transition-duration: 1s;
}
@media screen and (max-width: 1240px){
  :hover{
    width: 18px;
    height: 18px;
    transition-duration: 1s;
  }
}
`

const Promotion = styled.div`
width: 100%;
height: 48px;
display: flex;
font-size: 13px;
font-family:s-core-dream-light;
align-items: center;  
margin: 0 auto;
padding: 0 7%;
color: #4E4E4E;
font-weight: 600;
@media screen and (max-width: 1240px){
  font-size: 11px;
}
`

const PromotionText = styled.div`
font-family:s-core-dream-light;
margin-left: 1rem;
font-weight: 600;
animation: fadeout 2s;
animation-delay: 1s;
-webkit-animation: fadeout 2s;
-webkit-animation-delay: 1s;
@keyframes fadeout{
  from{
    opacity: 1;
  }
  to{
    opacity: 0;
  }
}

`

const Topic = styled.div``

const Topbar = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  padding: 10px 14%;
  background: #F5F7FF 0% 0% no-repeat padding-box;
  padding-left: 8%;
  box-sizing: border-box;
  font-size: 18px;
  margin-top: -1px;
`;

const RoomList = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 50px;
  margin: 0 auto;
  font-family: 's-core-dream-regular';
  font-weight: 600;
  & { 
    border-bottom: 1px solid #E9E9E9;
  }
`;

const RoomName = styled.div`
  display: flex;
  height: 100%;
  font-size: 18px;
  align-items: center;
  padding: 0 8%;
  &:hover {
    cursor: pointer;
  }
`;

const Zone = styled.div`
  border-radius: 10px;
  border: 2px solid ${props => colors[props.id - 1] || '#dd83ce'};
  font-size: 14px;
  padding: 2px 12px;
  line-height: 1.4;
  margin: 0 8%;
`;


export default ConsonantList;