/* global kakao*/
import { List } from "@material-ui/core";
import React, { useEffect } from "react";
import { select } from "redux-saga/effects";
import styled from "styled-components";
import Pin from '../static/icons/pin.svg';
import Selected from '../static/icons/selected.svg'


const MapInfo = ({ room, area, onSelectRoom, lat, lng, zoomLevel, setZoomLevel, name, setName }) => {


  useEffect(() => {

    let map = new kakao.maps.Map(document.getElementById("map"), { center: new kakao.maps.LatLng(36.77454690595654, 126.93253082534099), level: 3, maxLevel: 4 });
    let selected = null
    var mapTypeControl = new kakao.maps.MapTypeControl();
    map.addControl(mapTypeControl, kakao.maps.ControlPosition.RIGHT);

    var zoomControl = new kakao.maps.ZoomControl();
    map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);

    let newpos = new kakao.maps.LatLng(lat, lng);



    // kakao.maps.event.addListener(map, "dblclick", function (mouseEvent) { alert(mouseEvent.latLng.toString()); }); 좌표검색

    room = room.map((r) => {
      let pos = new kakao.maps.LatLng(r.latitude, r.longitude);
      let imageSize = new kakao.maps.Size(20, 40)
      let imageOption = { offset: new kakao.maps.Point(15, 50) }
      let markerImage = new kakao.maps.MarkerImage(Pin, imageSize, imageOption)
      let selectedImage = new kakao.maps.MarkerImage(Selected, imageSize, imageOption)
      let marker = new kakao.maps.Marker({ map, position: pos, image: markerImage });
      let content = '<span style=font-family:s-core-dream-medium> <span style= font-size:12px; >' + r.name + '</span>' + `</span>`;
      marker.setMap(null);
      if (r.isVisible === false)
        content = ''
      let overlay = new kakao.maps.CustomOverlay({ map, position: pos, content });
      overlay.setMap(null);


      if (lat !== "36.77454690595654" && lng !== "126.93253082534099") {

        if (name == r.name) {
          marker.setImage(selectedImage);
        }
      }

      kakao.maps.event.addListener(marker, "click", () => {
        room.map(r => {
          r.marker.setImage(markerImage)
        })
        map.setLevel(1)
        map.panTo(pos);
        if (selected !== null)
          selected.setImage(markerImage)
        marker.setImage(selectedImage)
        selected = marker
        onSelectRoom(r);
        document.getElementById("Room").scrollTo(0, 0)

      });



      return { marker, overlay, r };
    });
    kakao.maps.event.addListener(map, "zoom_changed", () => {

      if (map.getLevel() <= 2) {
        room.map(r => {
          r.marker.setMap(map);
          r.overlay.setMap(map);
        })
      } else {
        room.map(r => {
          r.marker.setMap(null);
          r.overlay.setMap(null);
        })
      }
    }

    );


    area = area.map((a) => {
      let center = new kakao.maps.LatLng(a.latitude, a.longitude);
      let circle = new kakao.maps.Circle({ center, fillColor: a.color, fillOpacity: 0.7, radius: 80, strokeWeight: 0, strokeOpacity: 0.7 });
      let html = `<div class="label"><span class="left"></span><span class="center" style="font-size:23px;font-family:s-core-dream-medium; ">${a.name}</span><span class="right"></span></div>`;
      kakao.maps.event.addListener(circle, "mouseover", () => { circle.setOptions({ fillOpacity: 1 }); });
      kakao.maps.event.addListener(circle, "mouseout", () => { circle.setOptions({ fillOpacity: 0.7 }); });
      kakao.maps.event.addListener(circle, "click", () => { map.setLevel(2); map.panTo(center); });
      new kakao.maps.CustomOverlay({ position: center, content: html }).setMap(map);
      circle.setMap(map);
      return circle;
    });


    kakao.maps.event.addListener(map, "zoom_changed", () => { (map.getLevel() <= 2) ? area.map((c) => { c.setMap(null); }) : area.map((c) => { c.setMap(map); }) })

    map.setLevel(zoomLevel);
    if (lat !== "36.77454690595654" && lng !== "126.93253082534099") {
      map.setLevel(1);
      map.panTo(newpos);

      newpos = [];

    }


  }, [room, area, lat, lng]);

  return <StyledMap id="map" />
};


const StyledMap = styled.div`
  width: 64%;
  height: 100%;
  z-index: 1;
`;


export default MapInfo;