import axios from 'axios'
const API = axios.create({ withCredentials: true })

export const getRooms   = () => API.get(`/room/read`)
export const getAd     = () => API.get(`/ads`)
export const getAreas   = () => API.get(`/location/read`)
export const getPhotos  = (id) => API.get(`/pictures/read/${id}`)


export const getAssign          = () => API.get(`/v1/assign`)
export const getOneRoom         = (id) => API.get(`/v1/readRoom/${id}`)
export const getAdRoom          = (id) => API.get(`/v1/readAdRoom/${id}`)
export const checkOneWish       = (id) => API.get(`/v1/checkWish/${id}`)
export const unCheckOneWish     = (id) => API.get(`/v1/unCheckWish/${id}`)
export const getPhoneNumber     = (id) => API.get(`/v1/getPhoneNumber/${id}`)
export const getRoomsWithOption = (options) => API.post(`/v1/searchRoom`, { options })