import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { changeStatus } from '../modules/setting.jsx'
import Header from '../components/Header.jsx'
import { useLocation, useHistory } from 'react-router'
import { getAssign } from '../services/api'

const HeaderContainer = () => {
  const { status } = useSelector(({ setting }) => ({
    status: setting.status
  }))
  const dispatch = useDispatch()
  const handleStatus = (id) => { dispatch(changeStatus(id)) }
  const location = useLocation()
  const history = useHistory()


  useEffect(() => {
    if (status !== -1 && location.pathname === '/Intro') history.push('/')
  }, [status])
  const handleAssign = () => { getAssign() }
  return (
    <Header handleStatus={handleStatus} handleAssign={handleAssign} />
  )
}

export default HeaderContainer