import { createAction, handleActions } from "redux-actions"

const CHANGE_STATUS = 'setting/CHANGE_STATUS'


export const changeStatus = createAction(CHANGE_STATUS, id => id)
const initialState = {
  prevStatus: 0,
  status: 0,
}

const setting = handleActions({
  [CHANGE_STATUS]: (state, { payload: id }) => ({
    ...state,
    prevStatus: state.status,
    status: id
  }),
}, initialState)

export default setting