import React from "react";
import styled from "styled-components";
import ConsonantList from '../common/ConsonantList.jsx'
import RightIcon from '../static/icons/right.svg'
import CloseIcon from '../static/icons/close.svg'

const SearchResult = ({ ad, search, onSelectRoom, handleStatus, setLat, setLng, setName }) => {
  return (
    <Container>
      <Header>
        <IconWrapper>
          <BackIcon src={RightIcon} onClick={() => handleStatus(1)} alt="RightIcon" />
        </IconWrapper>
        <HeaderTitle>검색결과</HeaderTitle>
        <IconWrapper>
          <img src={CloseIcon} onClick={() => handleStatus(0)} alt="CloseIcon" />
        </IconWrapper>
      </Header>
      <ConsonantList ad={ad} result={search} onSelectRoom={onSelectRoom} setLat={setLat} setLng={setLng} setName={setName} />
    </Container>
  );
};

const BackIcon = styled.img`
width: 1rem;
`



const Container = styled.div`
  width: 36%;
  height: auto;
  overflow-y: scroll;
  overflow-x: hidden;
  padding-bottom: 100px;
  /* -ms-overflow-style: none;  */

`;

const Header = styled.div`
  display: flex;
  width: 100%;
  padding: 18px 0;
  font-size: 24px;
  align-items: center;
  background: #DCE2FF 0% 0% no-repeat padding-box;
  font-family: s-core-dream-medium;
  text-align: center;
`;

const IconWrapper = styled.div`
    flex: 1;
  font-size: 10px;
  margin: 0;
  > *:hover {
    cursor: pointer;
  }
`

const HeaderTitle = styled.div`
  flex: 3;
  font-size: 28px;

  @media screen and (min-width: 480px) and (max-width: 1440px) {
    font-size: 24px;
  }
`

