import React from "react";
import styled from "styled-components";
import Carousel from "../common/Slider.jsx";
import RightIcon from '../static/icons/right.svg'
import BlankHeart from "../static/icons/BlankHeart.svg";
import FireFacility from "../static/icons/firefighter.svg";
import FillHeart from "../static/icons/FillHeart.svg";
import ReFrigerator from "../static/icons/refrigerator.svg";
import Microwave from "../static/icons/microwave.svg";
import Gasrange from "../static/icons/gasrange.svg";
import Washer from "../static/icons/laundry.svg";
import Veranda from "../static/icons/veranda.svg";
import Elevator from "../static/icons/elevator.svg";
import Postbox from "../static/icons/postbox.svg";
import Parking from "../static/icons/parking.svg";
import Aircon from "../static/icons/aircon.svg";
import Call from "../static/icons/Call.svg";
import CCTV from "../static/icons/cctv.svg";
import TV from "../static/icons/tv.svg";
import Wifi from "../static/icons/wifi.svg";
import Desk from "../static/icons/desk.svg";
import Bed from "../static/icons/bed.svg";
import PhoneModal from "../common/Modal.jsx";

const Room = ({ handlePhone, roomInfo, photo, checkWish, unWish, touched, movePrevious, handleModal, modal }) => {
  const { id, name, phone, yearPrice, halfPrice, monthPrice, charterPrice, fee, isMany, isWatertax, isElectax, isGastax,
    gasType, gasrangeType, address, comment, isBed, isFrige, isAircon, isWasher, isVeranda, isTV, isDesk, isMicrowave, isRouter, isElevator,
    isCctv, isPostbox, isParking, isFireFacility, locationId, isPet, isLH, maintenaceCost } = roomInfo;

  return (
    <FullContainer id="Room">
      <HeaderContainer>
        <RoomClose>
          <BackIcon src={RightIcon} onClick={movePrevious} />
        </RoomClose>
        <Title>{name}</Title>
        <IconSection>
          <div>
            {modal ? <PhoneModal handleModal={handleModal} name={name} phone={phone} /> : ''}
            <img src={Call} onClick={() => { handleModal(); handlePhone(id); }} alt="CallIcon" />
          </div>
          <div>
            <WhiteHeart src={touched ? FillHeart : BlankHeart} onClick={touched ? () => unWish(id) : () => checkWish(id)} />
          </div>
        </IconSection>
      </HeaderContainer>
      <RoomPhoto>
        <Carousel photo={photo} />
      </RoomPhoto>
      <ContentsSection>
        <RoomPriceContainer>
          <RoomPrice>년세 <span>{yearPrice}</span></RoomPrice>
          <RoomPrice>반년세 <span>{halfPrice ? halfPrice : '-'}</span></RoomPrice>
        </RoomPriceContainer>
        <OptionContainer>
          <OptionHeader>
            상세정보
          </OptionHeader>
          <InfoList>
            <Info title="구역" value={locationId} suffix="구역" />
            <Info title="보증금" value={fee} />
            <Info title="개인부담" value={'' + (isWatertax ? '수도세 ' : '') + (isElectax ? '전기세 ' : '') + (isGastax ? '가스비' : '')} not="없음" multiple />
            <Info title="월세" value={monthPrice} />
            <Info title="전세" value={charterPrice ? charterPrice + ' / ' + maintenaceCost : '-'} />
            <Info title="LH전세" value={isLH} not="불가" find="가능" />
            <Info title="반려동물" value={isPet} not="불가" find="가능" />
            <Info title="난방시설" value={gasType} list="도시가스,심야전기,LPG" />
            <Info title="거주인원" value={isMany} list="최대 1인, 최대 2인, 최대 3인" />
            <Info title="도로명주소" value={address} />
          </InfoList>
        </OptionContainer>
        <OptionContainer>
          <OptionHeader>실내옵션</OptionHeader>
          <OptionList>
            <Option title={isAircon ? "에어컨" : null} src={Aircon} />
            <Option title={isRouter ? "공유기" : null} src={Wifi} />
            <Option title={isFrige ? "냉장고" : null} src={ReFrigerator} />
            <Option title={isMicrowave ? "전자레인지" : null} src={Microwave} />
            <Option title={isWasher ? "세탁기" : null} src={Washer} />
            <Option title={isTV ? "TV" : null} src={TV} />
            <Option title={isVeranda ? "베란다" : null} src={Veranda} />
            <Option title={isBed ? "침대" : null} src={Bed} />
            <Option title={isDesk ? "책상" : null} src={Desk} />
            <Option title="없음,가스레인지,전기레인지" value={gasrangeType} multiple src={Gasrange} />
          </OptionList>
        </OptionContainer>
        <OptionContainer>
          <OptionHeader>실외옵션</OptionHeader>
          <OptionList>
            <Option title={isElevator ? "엘레베이터" : null} src={Elevator} />
            <Option title={isCctv ? "CCTV" : null} src={CCTV} />
            <Option title={isPostbox ? "무인택배함" : null} src={Postbox} />
            <Option title={isParking ? "주차장" : null} src={Parking} />
            <Option title={isFireFacility ? "소방시설" : null} src={FireFacility} />
          </OptionList>
        </OptionContainer>
        <OptionContainer>
          <OptionHeader>사장님 한마디</OptionHeader>
          <Descriptor dangerouslySetInnerHTML={{ __html: comment }} />
          <DescriptorText>※ 공시가와 실거래가는 차이가 있을 수 있습니다.</DescriptorText>
        </OptionContainer>
        <BottomSpace />
      </ContentsSection>
    </FullContainer>
  );
};

const Info = ({ title, value, suffix = '', not = '-', find, list, multiple }) => {
  value = (multiple === true) ?
    (value.length === 4 ? value : value.split(" ").toString())
    : value
  return (
    <InfoContainer>
      <InfoProperty>{title}</InfoProperty>
      {list ?
        <InfoValue>{list.split(",")[value]}</InfoValue>
        :
        <InfoValue>{value ? (find ? find : value + suffix) : not}</InfoValue>
      }
    </InfoContainer>
  )
}

const Option = ({ title, src, value, multiple }) => {
  title = (multiple === true) ? (value !== 0 ? title.split(",")[value] : '') : title
  return (<>
    {title ?
      <OptionBox>
        <OptionIconBox src={src} />
        <OptionText>{title}</OptionText>
      </OptionBox> : ""
    }
  </>)
}

const BottomSpace = styled.div`
height: 5rem;
`

const FullContainer = styled.div`
  width: 36%;
  display: block;
  height: 100%;
  margin: 0 auto;
  position: relative;
  overflow-x: hidden;
  overflow-y: scroll;
  @media screen and (min-width: 480px) and (max-width: 1440px) {
    height: 90%;
  }
`;
const ContentsSection = styled.div`
  display: flex;
  flex-wrap: wrap;
`

const HeaderContainer = styled.div`
  display: flex;
  flex: 100%;
  background-color: #dce2ff;
  font-size: 28px;
  padding: 18px 0;
  font-family: s-core-dream-medium;
  align-items: center;
  text-align: center;
  @media screen and (min-width: 480px) and (max-width: 1440px) {
    font-size: 24px;
    padding: 12px 0;
  }
`;
const RoomClose = styled.div`
  flex: 2;
  margin-top: 2px;
  font-size: 10px;
  font-family: 's-core-dream-regular';
  margin: 0;
  > * {
    cursor: pointer;
  }
`
const IconSection = styled.div`
  flex: 2;
  margin-top: 2px;
  font-size: 10px;
  font-family: s-core-dream-regular;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: baseline;
  > div {
    margin: 0 10px;
    cursor: pointer;
  }
`
const Title = styled.div` 
  flex: 6; 
`

const RoomPhoto = styled.div`
  display: block;
  width: 100%;
  height: 500px;
  `;

const RoomPriceContainer = styled.div`
  background-color: #f5f7ff;
  display: flex;
  flex-basis: 100%;
  height: 8%;
  padding: 12px 0;
  `;

const RoomPrice = styled.div`
  flex: 1;
  font-family: 's-core-dream-light';
  font-size: 28px;
  text-align: center;
  align-self: center;
  & + & {
    border-left: 1px solid #e0e0e0;
  }
  > span {
    font-size: 32px;
    font-family: 's-core-dream-medium';
  }
  @media screen and (min-width: 480px) and (max-width: 1440px) {
    font-size: 20px;
    > span {
      font-size: 24px;
    }
  }
`;

const OptionContainer = styled.div`
  flex-basis: 100%;
  border-bottom: 2px solid #e9e9e9;
  margin: 20px 40px 0;
  padding: 20px 0;
  `;

const InfoList = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  padding-left: 8px;
  font-size: 20px;
  @media screen and (min-width: 480px) and (max-width: 1440px) {
    font-size: 16px;
  }
  `

const InfoContainer = styled.div`
  display: flex;
  padding: 4px;
  `;

const InfoProperty = styled.div`
  flex-basis: 25%;
  font-family: 's-core-dream-medium';
  `;

const InfoValue = styled.div`
  flex-basis: 75%;
  color: #212121;
  font-family: s-core-dream-regular;
  `;

const OptionHeader = styled.div`
  margin: 0 2% 4%;
  font-family: s-core-dream-medium;
  font-size: 26px;
  `;

const OptionList = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  text-align: center;
  `

const OptionBox = styled.div`
  flex-basis: 20%;
  `;

const OptionIconBox = styled.img`
@media(max-width: 1440px){
  width: 37%;
}
`;

const OptionText = styled.div`
  padding: 10px 0;
  font-size: 12px;
  `;

const Descriptor = styled.div`
  font-family: 's-core-dream-light';
  font-size: 20px;
  padding: 0 12px;
  @media screen and (min-width: 480px) and (max-width: 1440px) {
    font-size: 16px;
  }
`;

const DescriptorText = styled.div`
  display: block;
  font-family: 's-core-dream-light';
  font-size: 16px;
  padding: 20px 0 20px 12px;
`
const BackIcon = styled.img`
width: 1rem;
`

const WhiteHeart = styled.img`
width: 28px;
`;

export default Room;